import React from 'react';

//头像简介
const HeadPor = () => (
    <div className="head-por"><img alt="" /></div>
)

//个人简介
const Info = () => (
    <div className="contacts-me">
        <p className="contacts-title">How Can I Help You?</p>
        <p className="contacts-discription">We have a whole team of talented experts ready to answer your questions and help you address any issues or concerns you may be experiencing with any of the company’s kits, sensors or modules. If you need some information or guidance, please get in touch with us!</p>
        <button className='contacts-email'><a className='contacts-href' href="mailto:zidong0822@163.com">Email Me</a></button>
    </div>
)

export default () => (
    <div className="container">
        <Info />
    </div>
)



